import HeaderPage from "../../components/Layout/HeaderPage";
import { Link, useHistory } from 'react-router-dom';
import { FaPlus } from "react-icons/fa";
import {
  Grid,
  GridColumn as Column
} from "@progress/kendo-react-grid";
import useDimensionTable from "../../hooks/useDimensionTable";
import useLoaderTable from "../../hooks/useLoaderTable";
import { Dropdown, Label, Image } from "semantic-ui-react";
import { deleteProducto } from "../../actions/productosActions";
import { Confirm, Loading } from "notiflix";

const Productos = () => {

  useDimensionTable();
  const history = useHistory();
  const { dataResul, dataState, dataStateChange, requestDataIfNeeded } = useLoaderTable({ url: 'product' });

  const handleEditar = (idProducto) => {
    history.push(`/productos/ficha/${idProducto}`);
  }


  const handleEliminar = async ({ id, name }) => {
    Confirm.show(
      'PRODUCTOS',
      `¿Desea eliminar al producto ${name}?`,
      'Aceptar',
      'Cancelar',
      async function(){
        Loading.pulse();
        const res = await deleteProducto(id);

        if(res) {
          requestDataIfNeeded(true);
        } else {
          Loading.remove();
        }
      }
    )
    }

  return ( 
    <div className="Productos">
       <HeaderPage>
        <div class="item ui colhidden">
          <button class="ui icon button" onClick={() => requestDataIfNeeded(true)}>
            <i class="sync icon"></i>
          </button>
        </div>
        <div className="item ui colhidden">
          <div className="ui big breadcrumb">
            <h2 className="section active">PRODUCTOS</h2>
          </div>
        </div>
        <div className="item ui colhidden">
          <Link to="/productos/ficha" className="ui button">
            <FaPlus /> Nuevo producto
          </Link>
        </div>
      </HeaderPage>
      <div className="Productos__body app__container_body">
        <div className="Productos__tabla">
          <Grid
              sortable={true}
              pageable={true}
              {...dataState}
              {...dataResul}
              onDataStateChange={dataStateChange}
              className="grid"
            >
              <Column
                width={100}
                cell={({ dataItem }) => (
                  <td style={{overflow: "inherit"}}>
                    <Dropdown
                      icon='ellipsis vertical'
                      floating
                      button
                      className='icon circular compact ui left pointing'
                    >
                      <Dropdown.Menu>
                        <Dropdown.Item
                          icon="edit"
                          className="blue"
                          text='Editar'
                          onClick={() => handleEditar(dataItem.id)}
                        />
                        <Dropdown.Item
                          icon="delete"
                          className="red"
                          text='Eliminar'
                          onClick={() => handleEliminar({...dataItem})}
                        />
                      </Dropdown.Menu>
                    </Dropdown>
                  </td>
                )}
              />
              <Column
                width={70}
                title='ID'
                field="id"
              />
              <Column 
                title="IMAGEN"
                width={250}
                cell={({ dataItem }) => (
                  <td>
                    {(dataItem.images).length > 0 ? (
                      <Image width={60} src={`${process.env.REACT_APP_HOST_URL}/${dataItem.images[0].path}`} />
                    ) : <Image width={60} src='' />}
                  </td>
                )}
              />
              <Column
                // width={400}
                title='PRODUCTO'
                field="name"
              />
              <Column 
                title="CATEGORÍA"
                // width={300}
                cell={({ dataItem }) => (
                  <td>
                    <Label color="purple" size="tiny">{dataItem.categories.name}</Label>
                  </td>
                )}
              />
              {/* <Column 
                title="MARCA"
                width={300}
                cell={({ dataItem }) => (
                  <td>
                    <Label color="red" size="tiny">{dataItem.brands.name}</Label>
                  </td>
                )}
              /> */}
            </Grid>
        </div>
      </div>
    </div>
  );
}
 
export default Productos;